@media screen and (max-width: 1200px) {
  .welcome-message-parent {
    gap: 4.188rem;
  }
}
@media screen and (max-width: 1050px) {
  .contact-info {
    display: none;
  }
  .welcome-back-shivendra {
    font-size: 3.5rem;
  }
  .finance5,
  .network,
  .project {
    font-size: var(--font-size-17xl);
  }
  .frame-parent1 {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 750px) {
  .contact-info {
    gap: var(--gap-6xl);
  }
  .welcome-message-parent {
    gap: 2.063rem;
  }
  .home1 {
    gap: var(--gap-31xl);
    padding-left: var(--padding-6xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .welcome-back-shivendra {
    font-size: 2.625rem;
  }
  .project {
    font-size: var(--font-size-8xl);
  }
  .frame-wrapper3 {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .frame-parent2 {
    gap: var(--gap-2xl);
  }
  .finance5 {
    font-size: var(--font-size-8xl);
  }
  .frame-wrapper4 {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .frame-parent3 {
    gap: var(--gap-2xl);
  }
  .network {
    font-size: var(--font-size-8xl);
  }
  .frame-parent4 {
    gap: var(--gap-2xl);
  }
  .welcome-message-parent {
    gap: var(--gap-mid);
  }
  .home1 {
    gap: var(--gap-6xl);
  }
}
